*{margin: 0;padding: 0;box-sizing: border-box;}
header{
  background-color: #D9967E;
  padding: 20px 10px;
  font-weight: bold;
  width: 60%;
  margin: auto;
  color: #8C441B;
  text-align: center;
  font-size: 1.6rem;
}
.list{
  position: relative;
  padding: 20px 10px;
  border: 1px solid #E3D8F2;
  display: flex;
  flex-wrap: wrap;
  width: 60%;
  margin: auto;
}
.listname{
  width: calc(100% - 100px);
  font-weight: bold;
  font-size: 1.5rem;
  color: #8C441B;
}
.good{
  position: absolute;
  bottom: 30px;
  right: 20%;
  font-size: 1.3rem;
}
.good>span{
  margin: 0 10px;
  cursor: pointer;
}
.date {
  width: 100%;
  font-size: 0.75rem;
  padding-top: 10px;
  color: #D9967E;
}
.modal  {
  background-color: #F2911B;
  padding: 20px;
  position: absolute;
  top: 100px;
  right: 5%;
  width: 30%;
  color: whitesmoke;
  border-radius: 5px;
  z-index: 10;
}
.modal h4{
  position: relative;
  font-size: 1.5rem;
  padding: 10px 0;
}
.modal h4::after{
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #F2B33D;
  left: 0;
  bottom: 0px;
}

.input {
  position: relative;
  width: 60%;
  margin: auto;
  padding: 10px;
  border: 1px solid #E3D8F2;
}
input{
  border: 1px solid #D9967E;
  border-radius: 5px;
  outline: none;
  margin: 5px 10px;
  padding: 0 5px;
}
textarea{
  width: 200px;
  border: 1px solid #D9967E;
  border-radius: 5px;
  outline: none;
  margin: 5px 10px;
  padding: 5px;
}
textarea::placeholder,input::placeholder{
  color: #F2911B;
}
textarea:focus::placeholder{
  opacity: 0;
}
textarea:focus{
  border: 2px solid #D9967E;
}
.i_name>input{
  height: 30px;
  width: 200px;
}
input:focus{
  border: 2px solid #D9967E;
}
input:focus::placeholder{
  opacity: 0;
}
.input label {
  color: #8C441B;
  font-size: 1.2rem;
}
.i_txt {
  display: flex;}

.txt{
  width: 100%;
  color: #D9967E;
}

.trans{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.btn {
  position: relative;
  background: #D9967E;
  width: 130px;
  height: 40px;
  line-height: 42px;
  padding: 0;
  border: none;
  margin: 10px;
}
.btn span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.btn::before,
.btn::after {
  position: absolute;
  content: "";
  right: 0;
  top: 0;
  background: #8C441B;
  transition: all 0.3s ease;
}
.btn::before {
  height: 0%;
  width: 2px;
}
.btn::after {
  width: 0%;
  height: 2px;
}
.btn:hover{
   background: transparent;
  box-shadow: none;
}
.btn:hover::before {
  height: 100%;
}
.btn:hover::after {
  width: 100%;
}
.btn span:hover{
   color: #8C441B;
}
.btn span::before,
.btn span::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
   background: #8C441B;
  transition: all 0.3s ease;
}
.btn span::before {
  width: 2px;
  height: 0%;
}
.btn span::after {
  width: 0%;
  height: 2px;
}
.btn span:hover::before {
  height: 100%;
}
.btn span:hover::after {
  width: 100%;
}